*:focus {
  -webkit-tap-highlight-color: transparent;
  outline: none;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
}
html,
body {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-size: 1.5vh;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  --main-highlight-color: #bb3f0e;
}
.app {
  box-sizing: border-box;
  min-height: 100vh;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
}
.app > * {
  padding: 1.5em 1.5em;
}

h1 {
  font-size: 1.8em;
  font-weight: 900;
  padding: 0;
  margin: 0;
}
h2 {
  font-size: 1em;
  padding: 0;
  margin: 0;
}

button {
  color: black !important;
  background-color: white;
  border: none;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.25);
  border-radius: 1.5em;
  padding: 0.5em 0.8em;
  margin: 0.5em 0;
}
main.mainMain {
  display: flex;
  flex-flow: column nowrap;
  gap: 1em;
}

@media (orientation: landscape) {
  main.mainMain {
    padding: 1.5em 25vmin;
  }
}

header.mainHeader {
  background-color: var(--main-highlight-color);
}
header.mainHeader section.headerText {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
header.mainHeader section.headerText h1 {
  color: white;
}

header.mainHeader button {
  margin-left: 1em;
}

footer.mainFooter {
  background-color: var(--main-highlight-color);
  text-align: center;
}

footer.mainFooter a,
footer.mainFooter p {
  color: white;
  font-weight: 700;
  line-height: 0.5em;
}

.game {
  text-align: center;
}
.actionButtons button {
  margin: 0.5em;
}
.actionButtons p {
  line-height: 1em;
  margin: 0.5em 0;
  min-height: 1em;
}

.guessText {
  margin: 0.5em 0;
  min-height: 1em;
  line-height: 1em;
  font-weight: 700;
  font-size: 1.8em;
}

.guessText p {
  margin: 0;
  padding: 0;
}

.game .message {
  position: absolute;
  background-color: rgba(187, 63, 14, 1);
  color: white;
  font-weight: 900;
  padding: 1em 2em;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.25);
  border-radius: 1.5em;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -55%);
  font-size: 1.5em;
}

svg text {
  pointer-events: none;
}
svg.ladybug {
  max-height: 45vh;
  max-width: 90vw;
}
svg.ladybug > * {
  font-weight: 700;
  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10 and IE 11 */
  user-select: none; /* Standard syntax */
}

svg.ladybug .expression.visible {
  display: initial;
}
svg.ladybug .expression.hidden {
  display: none;
}

svg.ladybug .button:active {
  background-color: transparent;
}
